*
  box-sizing: border-box

main
  width: 800px
  margin: auto
  position: relative
  min-height: 90vh

#titre
  background-color: navy
  color: white
  font:
    size: 3em
    weight: bold
    family: serif
    style: italic
  text-align: center
  padding: 12px 12px
  border:
    radius: 10px 10px 10px 0
    bottom: 1px solid gray
  &:hover
    cursor: pointer

#gauche
  width: 135px
  position: absolute
  left: 0
  background-color: navy
  height: 100%
  color: white
  padding: 20px 5px
  font-weight: bold
  border-radius: 0 0 10px 10px
  .maj
    position: absolute
    top: 60%
    left: 50%
    transform: translateX(-50%)
    text-align: center
    color: white
    font:
      family: sans-serif
      size: 0.8em
      weight: 300
    &::before
      content: "Mis à jour "

#contenu
  position: absolute
  left: 150px
  padding: 20px
  background-color: rgb(200, 206, 211)
  min-height: calc(100% - 20px)
  margin:
    block: 10px
    right: 10px
  border-radius: 10px
  width: 645px
  p
    font-size: 14pt
    text-align: justify

nav
  a
    font:
      family: sans-serif
      size: 12px
      weight: bold
    color: rgba(white, 0.7)
    text-decoration: none
    padding: 0.5em
    display: block
    border-bottom: 1px solid gray
    &:hover
      background-color: lightgray
      color: black
    &:first-child
      border-top: 1px solid gray

h1,
h2
  padding-left: 0.5em
  border-bottom: 2px solid black

.horizontal-menu
  font:
    family: sans-serif
    size: 11px
  text-align: center
  margin-bottom: 1.5rem
  a
    text-decoration: none
    color: black
    //background-color: white
    background-color: rgb(232, 232, 243)
    font-weight: bold
    padding: 2px 3px
    border: 1px solid black
    border-radius: 3px
    &:visited
      color: black
    &:hover
      color: white
      background-color: rgba(navy, 0.5)

.list
  margin-left: 2em
  color: rgba(black, 0.6)
  &__item
    a
      text-decoration: none
      color: black
      padding: 2px 4px
      font-weight: 500
      &:hover
        color: white
        background-color: rgba(black, 0.4)
        border-radius: 3px
  &__comment
    font-style: italic
  &__fresh
    &::before
      content: "["
    &::after
      content: "]"
  &__head
    color: darkred
    font-weight: 900
    padding-left: 4px
    &::after
      content: " :"

.nouveau
  &::after
    content: " [nouveau]"
    color: red
    font:
      size: 0.8em
      weight: bold

.change
  &::after
    content: " [changé]"
    color: red
    font:
      size: 0.8em
      weight: bold
